@tailwind base;
@tailwind components;
@tailwind utilities;

@layer{
    .element-center {
        @apply flex justify-center items-center flex-col;
    }
    .container {
        @apply px-[30px] mx-auto
    }
    
    .btn{
        @apply bg-[#42b0d1] hover:bg-[#8cdae4] transition-all duration-200
    }
}
body{
    font-family: 'Roboto', sans-serif;
    background-color : #181e2a ;
}
h1{
    font-size:40px ;
    line-height : 40px
}